require('./member-manage.scss');
require('./member-manage.html');
require('../../components/base/base.js');

$(function() {

  $(".switch-box .checkbox").click(function() {
    $(this).hasClass('disabled') ? $(this).removeClass('disabled').attr('data-text', "停用") : $(this).addClass('disabled').attr('data-text', "启用");

    // ajax

  });

});



